import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";

import { Location } from "@angular/common";
import {HttpService }  from "../../../http.service";
import {SellerListingDetails} from "./seller-Listing-detail.service"

@Component({
  selector: 'app-listing-property-detail',
  templateUrl: './listing-property-detail.component.html',
  styleUrls: ['./listing-property-detail.component.css']
})
export class ListingPropertyDetailComponent implements OnInit {
  Lookingpostcode: string;
  ChainStatus: string;
  FinancialPosition: string;
  Type: string;
  Position: string;

  MaxAmount: string;
  matchStatus: string;
  sub: any;
  MinAmount: string;
  propertyId: string;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerLookingpostcode: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  username: any;
  usertitle: any;
  buyerPropertyFor: any;
  PropertyType: any;
  Ownership: any;
  LookingStreetname: any;
  Conditions: any;
  Maxbathroom: any;
  Maxreception: any;
  user: any;
  uid: any;
  getPropertyDetails=[]
  constructor(   private _Activatedroute: ActivatedRoute,
    private _router: Router,   private _location: Location,
    public HttpService : HttpService,
    public SellerListingDetails:SellerListingDetails) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.Lookingpostcode = params.get("Lookingpostcode");
      this.ChainStatus = params.get("ChainStatus");
      this.FinancialPosition = params.get("FinancialPosition");
      this.Type = params.get("PropertyType");
      this.Position = params.get("Position");
      this.MaxAmount = params.get("MaxAmount")
      this.MinAmount = params.get ("MinAmount")
      this.propertyId = params.get("id")
    });
    this.getBuyerPropertyDetails()
  }
  
  backClicked() {
    this._location.back();
  }


  //Get Buyer Detail Property 
  getBuyerPropertyDetails()
  {
    this.SellerListingDetails.getBuyerRequirement(this.uid,this.propertyId).then(res =>{
       this.buyingPosition = res.data().Position
       this.buyerFinancialPosition = res.data().FinancialPosition
       this.BuyerPropertyType  = res.data().PropertyType
       this.BuyerLookingpostcode  = res.data().Lookingpostcode
       this.BuyerRoommin  = res.data().Roommin
       this.BuyerRoomsmax  = res.data().Roomsmax
       this.username = res.data().username
       this.usertitle  = res.data().usertitle
       this.buyerPropertyFor  = res.data().PropertyFor
       this.PropertyType = res.data().PropertyType
       this.Ownership = res.data().Ownership
       this.LookingStreetname = res.data().LookingStreetname
       this.Conditions = res.data().Conditions
       this.Maxbathroom = res.data().Maxbathroom
       this.Maxreception = res.data().Maxreception
    })
  }
}
