import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";

import { Location } from "@angular/common";
import { HttpService } from "../../../http.service"
import { SellerNestimateDetails } from "./seller-nestimate-details.service"

@Component({
  selector: 'app-listing-selling-property-detail',
  templateUrl: './listing-selling-property-detail.component.html',
  styleUrls: ['./listing-selling-property-detail.component.css']
})
export class ListingSellingPropertyDetailComponent implements OnInit {
  Lookingpostcode: string;
  ChainStatus: string;
  FinancialPosition: string;
  Type: string;
  Position: string;

  MaxAmount: string;
  matchStatus: string;
  sub: any;
  MinAmount: string;
  Maxrooms: string;
  Maxreception: any;
  PropertyCondition: string;
  Maxbathrooms: string;

  price: any;
  postcode: any;
  Streetname: any;
  PropertyTypes: any;
  Maxroom: any;
  ownerships: any;
  PropertyFor: any;
  Bathrooms: any;
  Reception: any;
  userName: any;
  usertitle: any;
  propertyId: any
  features: any;
  user: any;
  uid: any;
  constructor(private _Activatedroute: ActivatedRoute,
    private _router: Router, private _location: Location,
    public HttpService: HttpService,
    public SellerNestimateDetails: SellerNestimateDetails) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.Lookingpostcode = params.get("Lookingpostcode");
      this.Type = params.get("PropertyType");
      this.MaxAmount = params.get("MaxAmount");
      this.Maxrooms = params.get("Maxrooms");
      this.Maxreception = params.get("Maxreception")
      this.PropertyCondition = params.get("PropertyCondition")
      this.Maxbathrooms = params.get("Maxbathrooms");
      this.propertyId = params.get("id")
    });
    this.getBuyerPropertyDetails()

  }
  backClicked() {
    this._location.back();
  }

  
  //Get Seller Detail Property 
  getBuyerPropertyDetails() {
    this.SellerNestimateDetails.getSellerRequirement(this.uid, this.propertyId).then(res => {
      this.userName = res.data().username
      this.usertitle = res.data().usertitle
      this.PropertyFor = res.data().PropertyFor
      this.price = res.data().MaxAmount
      this.postcode = res.data().Lookingpostcode
      this.Streetname = res.data().LookingStreetname
      this.PropertyTypes = res.data().PropertyType
      this.Maxroom = res.data().Maxrooms
      this.ownerships = res.data().ownership
      this.Bathrooms = res.data().Maxbathrooms
      this.Reception = res.data().Maxreception
      this.features = res.data().features

    })
  }
}
