import { Component, OnInit } from '@angular/core';
import {SelecteddetailareaService } from "./selecteddetailarea.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import {HttpService} from "../../http.service";
import { FormsService } from '../../Home/Property/fillFormBuyer/fillFormBuyer.service';

@Component({
  selector: 'app-selecteddetailarea',
  templateUrl: './selecteddetailarea.component.html',
  styleUrls: ['./selecteddetailarea.component.css']
})
export class SelecteddetailareaComponent implements OnInit {
  sub: any;
  Minamount: string;
  MaxAmount: string;
  ChainStatus: string;
  Conditions: string;
  Lookinpostcode: string;
  UserId: string;
  userDetail;
  username: any;
  email: any;
  Phone: any;
  PropertyType: string;
  PropertyFor: string;
  AgentUid: string;
  BuyerUid: string;
  SellerUid: string;
  address: any;
  UserDetails;
  sellerusername: any;
  selleremail: any;
  sellerPhone: any;
  selleraddress: any;

  constructor(public SelecteddetailareaService:SelecteddetailareaService,private route: ActivatedRoute,private _location: Location,public HttpService:HttpService,
    public FormsService:FormsService) { }

  ngOnInit() {
   

    this.UserDetails = JSON.parse(localStorage.getItem("AllUserDetail"));
    this.Minamount = this.UserDetails[0].Buyer.MinAmount
    this.MaxAmount = this.UserDetails[0].Buyer.MaxAmount
    this.Lookinpostcode = this.UserDetails[0].Buyer.Lookingpostcode
    this.PropertyFor = this.UserDetails[0].Buyer.PropertyFor
    this.ChainStatus = this.UserDetails[0].Buyer.Position

    this.FormsService.getUser(this.UserDetails[0].Buyer.UserId).subscribe((ref) => {
      ref.forEach((element) => {
        if(element.data().uid == this.UserDetails[0].Buyer.UserId)
        {
          this.username = element.data().Name
          this.email = element.data().email
          this.Phone = element.data().Phone
        }
      })
    })
      
    this.FormsService.getUser(this.UserDetails[0].Seller.UserId).subscribe((ref) => {
      ref.forEach((element) => {
        if(element.data().uid == this.UserDetails[0].Seller.UserId)
        {
          this.sellerusername = element.data().Name
          this.selleremail = element.data().email
          this.sellerPhone = element.data().Phone
          this.selleraddress = element.data().Currentaddress

        }
      })
    })


  }
  backClicked() {
    this._location.back();
  }
}
