import { Injectable } from '@angular/core';
import {
  AngularFirestore,
  AngularFirestoreCollection,
} from "@angular/fire/firestore";

@Injectable({
  providedIn: 'root'
})
export class HomeService {
  return:any
  buyerRef: AngularFirestoreCollection<unknown>;
    homeRef: AngularFirestoreCollection<unknown>;
    blogRef: AngularFirestoreCollection<unknown>;
    FooterRef: any;
    sellerRef: AngularFirestoreCollection<unknown>;
  constructor(private db: AngularFirestore) { 
    this.FooterRef = db.collection("Footer")
    this.homeRef = db.collection("HomepageImages")
    this.blogRef = db.collection("Blogs")
    this.buyerRef = db.collection("listingBuyer")
    this.sellerRef = db.collection("listingSeller")
  }



  getFooter(uid): Promise<any> {
    return   this.FooterRef.ref.get();
  }

  getHomePage(uid): Promise<any> {
    return   this.homeRef.ref.get();
  }

  getBlogs(uid): Promise<any>{
      return this.blogRef.ref.get();
  }


  getlistingBuyer(uid): Promise<any>{
    return   this.buyerRef 
    .doc(uid)
    .collection("requirements")
    .ref.get();
}


getlistingseller(uid): Promise<any>{
    return   this.sellerRef 
    .doc(uid)
    .collection("properties")
    .ref.get();
}
  }

 



