import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import {HttpService} from "../../../http.service";
import {MatchesSellerPending} from "./matches-seller-selected.service"
@Component({
  selector: "app-my-matches-to-sell-selected-detail",
  templateUrl: "./my-matches-to-sell-selected-detail.component.html",
  styleUrls: ["./my-matches-to-sell-selected-detail.component.css"],
})
export class MyMatchesToSellSelectedDetailComponent implements OnInit {
  sub: any;
  Lookingpostcode: string;
  ChainStatus;
  FinancialPosition;
  Type;
  Position;

  PriceRange;
  matchStatus: string;
  MaxAmount: any;
  propertyId: string;
  notificationContent: any;
  PropertyFor: string;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerLookingpostcode: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  username: any;
  usertitle: any;
  buyerPropertyFor: any;
  Ownership: any;
  LookingStreetname: any;
  Maxbathroom: any;
  Maxreception: any;
  PropertyType: any;
  Conditions: any;
  MinAmount: any;
  user: any;
  uid: any;
  BuyerPropertyId: string;
  BuyerUserId: string;
  constructor(
    private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    public HttpService:HttpService,
    public MatchesSellerPendingService:MatchesSellerPending
  ) {}

  ngOnInit() {

    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid;
    }

    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.Lookingpostcode = params.get("Lookingpostcode");
      // this.ChainStatus = params.get("ChainStatus");
      this.FinancialPosition = params.get("FinancialPosition");
      this.Type = params.get("PropertyType");
      this.Position = params.get("Position");
      this.matchStatus = params.get("matchStatus");    
      this.propertyId = params.get("PropBuyerId")  ;
      this.BuyerPropertyId = params.get("BuyerPropertyId")
      this.BuyerUserId = params.get("BuyerUserId")

      console.log(this.BuyerPropertyId)
      console.log(this.BuyerUserId)


      console.log(   this.propertyId )
    });
  

 this.getBuyerData()
  }
  backClicked() {
    this._location.back();
  }

  getBuyerData()
  {
    this.MatchesSellerPendingService.getBuyerRequirement(this.BuyerUserId,this.BuyerPropertyId ).then((res)=>{
      this.buyingPosition = res.data().Position
        this.buyerFinancialPosition = res.data().FinancialPosition
        this.BuyerPropertyType  = res.data().PropertyType
        this.BuyerLookingpostcode  = res.data().Lookingpostcode
        this.BuyerRoommin  = res.data().Roommin
        this.BuyerRoomsmax  = res.data().Roomsmax
        this.username = res.data().username
        this.usertitle  = res.data().usertitle
        this.buyerPropertyFor  = res.data().PropertyFor
        this.PropertyType = res.data().PropertyType
        this.Ownership = res.data().Ownership
        this.LookingStreetname = res.data().LookingStreetname
        this.Conditions = res.data().Conditions
        this.Maxbathroom = res.data().Maxbathroom
        this.Maxreception = res.data().Maxreception
        this.MinAmount = res.data().MinAmount
        this.MaxAmount  = res.data().MaxAmount
     })
  
  }
}
