import { Component, OnInit } from '@angular/core';
import { MatchesBuyerService } from "./matches-buyer.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { notification } from "../../../Model/notification"
import { HttpService } from "../../../http.service";

@Component({
  selector: 'app-matches-buyer',
  templateUrl: './matches-buyer.component.html',
  styleUrls: ['./matches-buyer.component.css']
})
export class MatchesBuyerComponent implements OnInit {
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  sub: any;
  PropertyType: string;
  ownership: string;
  features: string;
  matchStatus: any;
  MaxAmount: string;
  propertyId: any;
  return: any;
  uid: any;
  user: any;
  expressed: string;
  userId: string;
  notification: notification;
  now: Date = new Date();
  sellerProperty: any;
  sellerPropertys = [];
  matchesSeller: any;
  overLay: boolean = false;
  buyerProperty;
  sellerId: string;
  notificationOverlay: Object;
  notificationContent: any;
  price: any;
  postcode: any;
  Streetname: any;
  PropertyTypes: any;
  Maxroom: any;
  ownerships: any;
  PropertyFor: any;
  Bathrooms: any;
  Reception: any;
  userName: any;
  usertitle: any;
  BuyerPropertyId: string;
  BuyerUserId: string;
  SellerPropertyId: string;
  SellerUserId: string;
  buyerDetails: any;
  constructor(private MatchesBuyerService: MatchesBuyerService,
    private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    public HttpService: HttpService

  ) { }

  ngOnInit() {

  
    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid;
    }


    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.Lookingpostcode = params.get("Lookingpostcode");
      this.Lookingstate = params.get("Lookingstate");
      this.LookingAddress = params.get("LookingAddress");
      this.norooms = params.get("Roomsmax");
      this.PropertyCondition = params.get("PropertyCondition");
      this.MaxAmount = params.get("MaxAmount");
      this.PropertyType = params.get("PropertyType");
      this.ownership = params.get("ownership");
      this.features = params.get("features");
      this.matchStatus = params.get("matchStatus");
      this.propertyId = params.get("propertyId");
      this.expressed = params.get("expressed");
      this.userId = params.get("UserId")
      this.sellerId = params.get("sellerId")


      this.BuyerPropertyId = params.get("BuyerPropertyId")
      this.BuyerUserId = params.get("BuyerUserId")

      this.SellerPropertyId = params.get("SellerPropertyId")
      this.SellerUserId = params.get("SellerUserId")
    });
    this.getSellerDetails()
    this.getBuyerDetails()


    this.HttpService.getMatchesSeller().subscribe((sellerData) => {
      sellerData.forEach(element => {
        if (element.myId == this.userId && element.matchStatus == "pending") {
          this.buyerProperty = ({ detail: element, propertyId: element.id });
        }
      });
    })

    this.HttpService.getPropertyIdMatches().subscribe((data) => {
      data.forEach(element => {
        if (element.UserId == this.uid && element.propertyId == this.propertyId && element.Type == "BuyerSelected") {
          this.expressed = "true"
        }
      });
    })
    this.HttpService.getBlueNotificationContent().subscribe((data) => {
      this.notificationOverlay = data
      this.notificationContent = this.notificationOverlay[0].BuyerMatchesConfirmInterestNotification
    })


  }
  addToExpressCollection() {
    this.return = this.MatchesBuyerService.ExpressInterest(
      this.uid,
      this.propertyId.trim()
    ).then((data) => {
      if (data == true) {
        this.createSellerNotification();
        this.backClicked();
      }
    });
  }
  createSellerNotification()
  {
    this.notification={
      time:this.now,
    viewed:"Confirmed",
    userId:this.uid,
    Type:"Buyer_Confirmed_my_activity",
    propertyId:this.propertyId,
    Lastseen:null
    }
    this.return = this.MatchesBuyerService
    .createNotification(this.userId, this.notification)
      .then(data => {
      });
  }


  backClicked() {
    this._location.back();
  }


  createOther() {
    this.matchesSeller = {
      BuyerDetails:this.buyerDetails,
      BuyerPropertyId:this.BuyerPropertyId,
      BuyerUserId:this.BuyerUserId,
      SellerUserId:this.SellerUserId,
      SellerPropertyId:this.SellerPropertyId,        
      matchStatus: "confirm_interest"
        
          };
          this.return = this.MatchesBuyerService
            .matchesBuyerCreate(this.userId, this.matchesSeller)
            .then((data) => {
              if (data == true) {
                this.addToExpressCollection()
              }
            });
        }




  backOverlay() {
    this.overLay = false
  }
  connfirmOverLay() {
    this.overLay = true
  }

  getSellerDetails()
  {
    this.MatchesBuyerService.getSeller(this.SellerUserId , this.SellerPropertyId).then((res) => {
      this.userName = res.data().username
      this.usertitle = res.data().usertitle
      this.PropertyFor = res.data().PropertyFor
      this.price = res.data().MaxAmount
      this.postcode = res.data().Lookingpostcode
      this.Streetname = res.data().LookingStreetname
      this.PropertyTypes = res.data().PropertyType
      this.Maxroom = res.data().Maxrooms
      this.ownerships = res.data().ownership
      this.Bathrooms = res.data().Maxbathrooms
      this.Reception = res.data().Maxreception
    })
  }
getBuyerDetails()
{
  console.log(this.BuyerUserId , this.BuyerPropertyId)
  this.MatchesBuyerService.getBuyer(this.BuyerUserId , this.BuyerPropertyId).then((res) => {
    this.buyerDetails = res.data()
  })
}

  

}
