import { Component, OnInit } from '@angular/core';
import { flatMap } from 'rxjs/operators';
import { AgenthomeService } from "./agenthome.service";
import { AuthService } from "../auth.service"
import { AngularFireAuth } from "@angular/fire/auth";
import { HttpService } from "../http.service"
import { StrapiAuthService } from "../strapi-auth.service";
import { first } from 'rxjs/operators'
import { Router, ActivatedRoute } from "@angular/router";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
  MatAutocompleteSelectedEvent,
  MatAutocompleteTrigger,
  MatDialogConfig
} from "@angular/material";

import { Location } from "@angular/common";


import { AlertUserTypeComponent } from "../Misc/alert-user-type/alert-user-type.component"
@Component({
  selector: 'app-agenthome',
  templateUrl: './agenthome.component.html',
  styleUrls: ['./agenthome.component.css']
})
export class AgenthomeComponent implements OnInit {
  agents;

  user: any;
  uid: any;
  agentsBuyer = [];
  agentsSeller = [];
  agentNew = [];
  loggedIn: boolean = false
  signUp: any;
  password: any;
  name: any;
  email: any;
  userData: any;
  Companyname: any;
  Address: any;
  Postcode: any;
  Phonenumber: any;
  Jobtitle: any;
  SinglePercentage: any;
  Multipercentage: any;
  id: any;
  Image: any;
  BuyerDetail = [];
  emailField: boolean = false
  notValidEmail: boolean = false
  SignUp: boolean = false
  emailData;
  validity: any;
  Fullname: any;
  PropertyId: any;
  UserId: any;
  UserDetails;


  constructor(public AgenthomeService: AgenthomeService, public authService: AuthService, public afAuth: AngularFireAuth, public HttpService: HttpService, public StrapiAuthService: StrapiAuthService, private router: Router, private dialog: MatDialog,    public _location: Location, ) { }

  ngOnInit() {

    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid
    }
    this.userLogged()


    this.items()



    // this.HttpService.getValidityError().subscribe((validity) => {
    //   this.validity = validity[0].SigininValidityError
    // })


  }
  //SignIn Google
  googleLogin() {

    this.authService.GoogleAuth().then((data) => {
      this.loggedIn = true
      this.userLogged()
      this.items()
    });
  }

  //Signup Google
  googleSignup() {

    this.authService.GoogleAuthSignup().then((data) => {
      this.loggedIn = true
      this.items()
      this.userLogged()
    });
  }

  signIn(email, pass) {


    this.authService.SignIn(email, pass).then((data) => {
      window.location.reload()

      this.items()

      this.userLogged()

    });
  }

  keyDownFunction(event) {
    if (event.keyCode == 13) {
      this.signUp(this.name, this.email, this.password);
    }
  }


  items() {
   this.AgenthomeService.getAgentSeller(this.uid).then((data)=>{
     data.forEach(element => {
      this.BuyerDetail.push(element.data())
     });
     localStorage.setItem("AllUserDetail", JSON.stringify(this.BuyerDetail));

    this.UserDetails = JSON.parse(localStorage.getItem("AllUserDetail"));
   })


  }

  userLogged() {
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem("user"));
        this.loggedIn = true;
        this.agentItem()
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.loggedIn = false;
      }
    });

  }




  agentItem()
 {
    this.AgenthomeService.getAgent(this.uid).then((newData) => {
      newData.forEach(element => {
        this.Companyname = element.data().company
        this.Address =  element.data().address
        this.Postcode = element.data().postcode
        this.Phonenumber = element.data().phone
        this.Jobtitle = element.data().jobtitle
        this.SinglePercentage = element.data().solefees
        this.Multipercentage = element.data().multiplefees
        this.Image = element.data().image
        this.Fullname = element.data().fullname
        this.PropertyId = element.id
        this.UserId = this.user.uid
      });
    })
  }



  validateEmail(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (re.test(String(email).toLowerCase()) == true) {
      this.HttpService.getAllUsers(email).subscribe((data) => {
        this.emailData = data
        if (this.emailData.length == 1) {
          this.emailField = true
          this.notValidEmail = false

          if (this.emailData[0].AgentType == null) {


            this.emailField = false
            this.notValidEmail = true
            const dialogRef = this.dialog.open(
              AlertUserTypeComponent,
              {
                data: {
                  message: "Please Login as Agent",

                }
              }
            );


          }
        }
        else {
          this.notValidEmail = true

        }
      })
    }

    else {
      this.notValidEmail = true

    }

  }
}
