import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { notification } from "../../../Model/notification"
import { StateServiceService } from "../../../state-service.service";
import { SellerMatchesService } from "./seller-matches.service";
import { HttpService } from "../../../http.service";
import {CMSSERVICE} from "../../../cms.service"

@Component({
  selector: 'app-seller-matches',
  templateUrl: './seller-matches.component.html',
  styleUrls: ['./seller-matches.component.css']
})
export class SellerMatchesComponent implements OnInit {
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  sub: any;
  PropertyType: string;
  ownership: string;
  features: string;
  matchStatus: any;
  MaxAmount: string;
  propertyId: any;
  return: any;
  uid: any;
  user: any;
  expressed: any;
  Type: string;
  ChainStatus: string;
  Conditions: string;

  Position: string;
  notification: notification;
  now: Date = new Date();
  UserId: string;
  matchesSeller: any;
  sellerProperty = [];
  overLay: boolean = false
  id: string;
  buyerId: string;
  notificationOverlay: Object;
  notificationContent: any;
  PropertyFor: string;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerLookingpostcode: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  username: any;
  usertitle: any;
  buyerPropertyFor: any;
  Ownership: any;
  LookingStreetname: any;
  Maxbathroom: any;
  Maxreception: any;
  buyerPropertyId: string;
  buyerUserId: string;
  SellerPropertyID: string;
buyerDetails ; 
  constructor(private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    private SelectedMyMatchesService: SellerMatchesService,
    private stateService: StateServiceService,
    public HttpService: HttpService,
    public CMSSERVICE:CMSSERVICE) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid;
    }
    
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.Lookingpostcode = params.get("Lookingpostcode");
      this.ChainStatus = params.get("ChainStatus");
      this.Conditions = params.get("Conditions");
      this.Position = params.get("Position");
      this.MinAmount = params.get("MinAmount");
      this.MaxAmount = params.get("MaxAmount");
      this.Type = params.get("PropertyType");
      this.matchStatus = params.get("matchStatus");
      this.propertyId = params.get("propertyId").trim();
      this.expressed = params.get("expressed");
      this.PropertyFor = params.get("PropertyFor")

      this.buyerPropertyId = params.get("id");
      this.buyerUserId = params.get("UserId")
      this.SellerPropertyID = params.get("buyerId")




    });





    this.CMSSERVICE.getBlueNotification(this.uid).then((data)=>{
      data.forEach(element => {
        this.notificationContent =element.data().MynestimateBuyerDetails
      });
    })
   

    this.getBuyerData()

  }
  backClicked() {
    this._location.back();
  }


  backOverlay() {
    this.overLay = false
  }
  connfirmOverLay() {
    this.overLay = true
  }

  check() {
    this.HttpService.getPropertyIdMatches().subscribe((data) => {
      console.log(data.length)
      data.forEach(elements => {

        if (elements.Type == "Agent_confirmed" && elements.UserId == this.uid && elements.PropertyId == this.propertyId) {
          this.expressed = "true"
        }
        if (elements.Type != "Agent_confirmed" && elements.UserId != this.uid && elements.PropertyId != this.propertyId) {
          this.expressed = "false"
        }


      });
    })
  }


  getBuyerData()
  {
    this.SelectedMyMatchesService.getBuyerRequirement(this.buyerUserId,this.buyerPropertyId).then((res)=>{
      this.buyerDetails = res.data()
      this.buyingPosition = res.data().Position
        this.buyerFinancialPosition = res.data().FinancialPosition
        this.BuyerPropertyType  = res.data().PropertyType
        this.BuyerLookingpostcode  = res.data().Lookingpostcode
        this.BuyerRoommin  = res.data().Roommin
        this.BuyerRoomsmax  = res.data().Roomsmax
        this.username = res.data().username
        this.usertitle  = res.data().usertitle
        this.buyerPropertyFor  = res.data().PropertyFor
        this.PropertyType = res.data().PropertyType
        this.Ownership = res.data().Ownership
        this.LookingStreetname = res.data().LookingStreetname
        this.Conditions = res.data().Conditions
        this.Maxbathroom = res.data().Maxbathroom
        this.Maxreception = res.data().Maxreception
     })
  
  }
}

