import { Component, OnInit } from '@angular/core';
import {MatIconModule} from '@angular/material/icon';
import {AuthService} from "../../auth.service";
import {CMSSERVICE} from "../../cms.service";
import { AlertUserTypeComponent } from "../../Misc/alert-user-type/alert-user-type.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
  MatAutocompleteSelectedEvent,
  MatAutocompleteTrigger,
  MatDialogConfig
} from "@angular/material";
import {AdminPanelService} from "../admin-panel/admin-panel.service.ts.service"
@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css']
})
export class AdminPanelComponent implements OnInit {
  checked = false;
  isLoading: boolean = false
  emails;
  password;
 KnowUser =[];
 getUser:any;
 type:any = "Admin";
 LoggedIn: boolean = false
  uid: any;
  constructor(public AuthService:AuthService,
    public CMSSERVICE:CMSSERVICE,
    private dialog: MatDialog,
    public adminPanelService:AdminPanelService) {
    
   }

  ngOnInit() {
    this.getUser = JSON.parse(localStorage.getItem("user"));
  
  this.getUserDetails()


if(this.constructor.name == "AdminPanelComponent")
{
  document.getElementById("appfooter").style.display = "none"
  document.getElementById("header-main").style.display = "none"

  }

  }

getUserDetails()
{

  if(this.getUser != null)
  {
    this.uid = this.getUser.uid
  this.adminPanelService.getUser(this.uid).then((element)=>{
    if(element.data().UserType == "Admin")
    {
    this.LoggedIn = true
     }
  else if(element.data().UserType != "Admin")
  {
    
      const dialogRef = this.dialog.open(AlertUserTypeComponent, {
        data: { message: "Please Login as Admin" }
      });
      this.LoggedIn = false
  
    }
  })

}
}









  //getUser(uid)

  signIn(email, pass) {
    this.isLoading = true;
    this.CMSSERVICE.CheckUser(email,this.type).then((data)=>{
      data.forEach(element => {
        this.KnowUser.push(element.data())
      if(element.data().email == email && element.data().UserType != "Admin")
        {
          const dialogRef = this.dialog.open(AlertUserTypeComponent, {
            data: { message: "Please Login as Admin" }
          });
          this.isLoading = false;
        }
        else if(element.data().email == email && element.data().UserType == "Admin")
        {
          this.AuthService.SignIn(email, pass).then((data) => {
            this.isLoading = false;
            window.location.reload()
        });
        }
      });
      })
  }

}
