  submitForm(displayName, email, pass,User) {
    this.signUp(displayName, email, pass,User)
  }
  async upload(event) {
    this.isLoading = true
        const file = event.target.files[0];
        if (file) {
           const filePath = `${this.basePath}/${file.name}`;  // path at which image will be stored in the firebase storage
           this.task =  this.afStorage.upload(filePath, file);    // upload task
           // this.progress = this.snapTask.percentageChanges();
           (await this.task).ref.getDownloadURL().then(url => {
     
            this.downloadableURL = url; });  // <<< url is found here
 
          } 
          
          this.isLoading = false
       }


  
  RemoveImage()
  {
    this.imageUploaded = false
    this.files = null
  }
  signUp(displayName, email, pass,User) {
    this.isLoading = true;
    this.afAuth.auth.createUserWithEmailAndPassword(email, pass).then(res => {
      this.timer = timer(5000); // 5000 millisecond means 5 seconds
        this.subscription = this.timer.subscribe(() => {
      if(res != null)
      {    
        
        this.SetUserDatas(res.user.uid,displayName,User,email)

       
      // set showloader to false to hide loading div from view after 5 seconds
      this.stateService.agentSignup = this.agentSignup;
      this.agentSignup.image=this.downloadableURL 
      this.agentSignup.uid = res.user.uid
      this.agentSignup.Status = false
      this.return = this.AgentSignupService

        .createAgentCustomer(res.user.uid, this.agentSignup)
        .then(data => {
          if (data == true) {
            const dialogRef = this.dialog.open(
              AlertDialogAgentComponent,
              {
                data: {
                  message: "HelloWorld",
                  buttonText: {
                    cancel: "Done"
                  }
                }
              }
            );
           
          }
        });
  
       
      }
    });
    })
    .catch(error => {
      alert(error.message)
    });
    this.isLoading = true;
  }
  SetUserDatas(user,displayName,Users,email) {

    const userRef: AngularFirestoreDocument<any> = this.afs.doc(
      `users/`+ user
    );
    const userData:any= {
      uid: user,
      email: email,
      Name :displayName,
      UserType:Users,
      verified:false
    };
    return userRef.set(userData, {merge: true});


  }
  setTimer() {
    // set showloader to true to show loading div on view
    this.isLoading = true;
    this.timer = timer(10000); // 5000 millisecond means 5 seconds
    this.subscription = this.timer.subscribe(() => {
      // set showloader to false to hide loading div from view after 5 seconds
      this.isLoading = false;
      this.responseStrapi = JSON.parse(this.requests.response)
      this.ImageUrl = this.responseStrapi[0].url
      this.imageUploaded = true
    });
  }


