import { Component, OnInit } from '@angular/core';
import { MatCardModule } from "@angular/material/card";
import { AuthService } from "../../auth.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import {HttpService} from '../../http.service';
import {MyMatchesService} from "../../Menu/myMatches/myMatches.service";
import {NestimatesHomeService}  from "../nestimates-home/nestimates-home.service";
import {HomeMatchesService} from "../home-matches/home-matches.service"
import {HomeService} from "../home.service"

@Component({
  selector: 'app-clickhome',
  templateUrl: './clickhome.component.html',
  styleUrls: ['./clickhome.component.css']
})
export class ClickhomeComponent implements OnInit {
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoggedIn: Boolean = false;
  userData: any;
  boolean: boolean;
  isLoading: boolean = false;
  cookie:boolean =true;
 owner:any;
 seeker:any;
value:any
  private _user: firebase.User;
  home: any;
  home2: any;
  Policy: void;
  checkPolicy: any;
  propertyRequirementDetails:any= [];
  propertyDetails:any = [];
  propertyLength: any;
  sellerLength: any;
  uid: string;
  checkMatches:boolean = false;
  checkNumberNestimates : any;
  buyerPoperty: any;
  sellerProperty: any;
  appMatches:boolean = false
  Componame;
  homePageImage;
  imageUrl;
  secondImageUrl;
  TopImageText: any;
  SecondImageText: any;
  step3Text: any;
  step3Image: any;
  Step2Image: any;
  Step2Text: any;
  Step1Image: any;
  Step1TExt: any;
  how: any;
  homePageBlogs = [];

  public get user(): firebase.User {
    return this._user;
  }
  public set user(value: firebase.User) {
    this._user = value;
  }
  constructor(   public authService: AuthService,
    public afAuth: AngularFireAuth,
    private Router:Router,
    private HttpService:HttpService,
    private MatchesService:MyMatchesService,
    private myrequirement_service: NestimatesHomeService,
    public HomeService:HomeService) { }

    ngOnInit() {
      this.user = JSON.parse(localStorage.getItem("user"));
      if(this.user != null)
      {
      this.uid = this.user.uid;
      }
     
      this.checkPolicy = JSON.parse(localStorage.getItem("Policy"));
      if(this.checkPolicy==false)
      {
        this.cookie = false
      }
  
      this.afAuth.authState.subscribe(user => {
        if (user) {
          this.userData = user;
          localStorage.setItem("user", JSON.stringify(this.userData));
          this.isLoggedIn = true
    
        } else {
          localStorage.setItem("user", null);
          JSON.parse(localStorage.getItem("user"));
          this.LoggedOut();
          this.isLoggedIn = false
         
        }
      });
 
      this.HomepageImagesText()
      this.getBlogs()
    }
  
    HomepageImagesText()
    {
      this.HomeService.getHomePage(1).then((res)=>{
        res.forEach(element => {
          this.imageUrl = element.data().BannerImageUrl
          this.secondImageUrl = element.data().SecondBannerImage
          this.SecondImageText = element.data().SecondBannerText
          this.Step1Image = element.data().Step1Image
          this.Step2Image = element.data().Step2Image
          this.step3Image= element.data().Step3Image
          this.Step1TExt = element.data().Step1ImageText
          this.Step2Text = element.data().Step2ImageText
          this.step3Text = element.data().Step3ImageText
        });
      })
    }
  
    getBlogs()
    {
      this.HomeService.getBlogs(1).then((res)=>{
        res.forEach(element => {
          this.homePageBlogs.push(element.data())
        });
      })
     
    }
  
    private LoggedIn() {
      this.isLoggedIn = true;
      this.cookie=false;
    }
    private LoggedOut() {
      this.isLoggedIn = false;
    }
  
    facebookLogin() {
      this.isLoading = true;
  
      this.authService.FacebookAuth().then(data => {
        this.isLoading = false;
      });
    }
  
    googleLogin() {
      this.isLoading = true;
  
      this.authService.GoogleAuth().then(data => {
        this.isLoading = false;
        this.isLoggedIn = true;
      });
    }
  
    signIn(email, pass) {
      this.isLoading = true;
  
      this.authService.SignIn(email, pass).then(data => {
        this.isLoading = false;
        this.isLoggedIn = true;
      });
    }
  
  
    closeCookie()
    {
      this.cookie = false
      this.Policy=localStorage.setItem("Policy", JSON.stringify(this.cookie))
    }
  
    homeradio(value)
    {
     if(value ==this.home)
      {
      this.Router.navigate(["/fillFormBuyer/:Currentpostcode/:CurrentTown/:Currentstate/:Currentcountry/:Lookingpostcode/:LookingStreetname/:LookingTown/:Lookingstate/:Country/:FinancialPosition/:PropertyType/:Roommin/:Roomsmax/:MinAmount/:MaxAmount/:Validity/:Minbathroom/:Maxbathroom/:Minreception/:Maxreception/:Conditions/:Ownership/:CurrentAddress"]);
       }
     if(value == this.home2)
       {
       this.Router.navigate(["/fillformseller/:Lookingpostcode/:LookingAddress/:LookingTown/:Lookingstate/:PropertyType/:Maxrooms/:MaxAmount/:ownership/:Maxbathrooms/:Maxreception/:PropertyCondition/:Country"]);
       }
 
   
    }
    
  }
  
