import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { StrapiAuthService } from "../../strapi-auth.service";

@Component({
  selector: 'app-google-login-check',
  templateUrl: './google-login-check.component.html',
  styleUrls: ['./google-login-check.component.css']
})
export class GoogleLoginCheckComponent implements OnInit {
  idToken: any;
  accessToken: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    public StrapiAuthService: StrapiAuthService,
    public _router: Router,) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.idToken  = params['id_token'];
      this.accessToken = params['access_token']
    });

    this.StrapiAuthService.Googlelogin(this.idToken ,this.accessToken).pipe()
    .subscribe(
      data => {

        this._router.navigate(['/'])
  .then(() => {
    window.location.reload();
  });
      },
      error => {
        if (error && error.error && error.error.message && error.error.message.length > 0) {
          alert(error.error.message[0].messages[0].message);
        }
        else {
          alert('error');
        }

        //this.alertService.error(error);
  
      });

  }

}
