import { Component, OnInit } from '@angular/core';
import {AdminPanelService} from "../admin-panel/admin-panel.service.ts.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";

@Component({
  selector: 'app-users-edit-page',
  templateUrl: './users-edit-page.component.html',
  styleUrls: ['./users-edit-page.component.css']
})
export class UsersEditPageComponent implements OnInit {
  getUser: any;
  uid: any;
  name: any;
  image: any;
  sub: any;
  UserUid: string;
  title: any;
  email: any;
  DOB: any;
  Phone: any;
  Currentpostcode: any;
  Currentstate: any;
  CurrentTowncity: any;
  Currentaddress: any;
  username: any;

  constructor(public adminPanelService:AdminPanelService,
    private _Activatedroute: ActivatedRoute,
    private _location: Location,) { }

  ngOnInit() {
    this.getUser = JSON.parse(localStorage.getItem("user"));

    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.UserUid = params.get("uid");

    })

    this.getUserDetails()
    this.getSelectedUserDetail()
  }
  getUserDetails()
  {
  
    if(this.getUser != null)
    {
      this.uid = this.getUser.uid
      document.getElementById("appfooter").style.display = "none"
      document.getElementById("header-main").style.display = "none"
      this.adminPanelService.getUser(this.uid).then((element)=>{
      this.name = element.data().Name
      this.image = element.data().Image
    })
  }
  }
  backClicked() {
    this._location.back();
  }




  getSelectedUserDetail()
  {
  this.adminPanelService.getUser(this.UserUid).then((user)=>{

    this.username = user.data().Name

    this.email = user.data().email
  if(user.data().title != null)
  {
    this.title = user.data().title

  }
    if( user.data().DOB != null)
    {
     this.DOB = user.data().DOB.toDate() 
    }

    if(user.data().Phone != null)
    {
      this.Phone = user.data().Phone
    }

    if(user.data().Currentpostcode != null)
    {
      this.Currentpostcode = user.data().Currentpostcode
    }

    if(user.data().Currentstate != null)
    {
      this.Currentstate = user.data().Currentstate
    }
    if(user.data().CurrentTowncity != null)
    {
      this.CurrentTowncity = user.data().CurrentTowncity
    }
    if(user.data().Currentaddress != null)
    {
      this.Currentaddress = user.data().Currentaddress
    }
  })
  }
 
}
