import { Component, OnInit } from '@angular/core';
import {AdminPanelService} from "../admin-panel/admin-panel.service.ts.service"

@Component({
  selector: 'app-users-agent-tab', 
  templateUrl: './users-agent-tab.component.html',
  styleUrls: ['./users-agent-tab.component.css']
})
export class UsersAgentTabComponent implements OnInit {
  getUser: any;
  name: any;
  uid: any;
  image: any;
  allUser = []
  allAgent = []
  allUserDetails = []

  constructor( public adminPanelService:AdminPanelService) { }

  ngOnInit() {

    this.getUser = JSON.parse(localStorage.getItem("user"));
this. getUserDetails()
this. getCountUser()
this.getAllUsers()

  }
  getUserDetails()
  {
  
    if(this.getUser != null)
    {
      this.uid = this.getUser.uid
      this.adminPanelService.getUser(this.uid).then((element)=>{
      this.name = element.data().Name
      this.image = element.data().Image
    })
  
  }
  }


  getCountUser()
  {
    this.adminPanelService.getAllUser().then((element)=>{
     element.forEach(elements => {
       if(elements.data().UserType != "Agent"){
        this.allUser.push({detail:elements.data(),uid:elements.data().uid})
           }
       if(elements.data().UserType == "Agent")
       {
        this.allAgent.push(elements.data())
       }
     });

    })
  }


  getAllUsers()
  {
    this.adminPanelService.getAllUser().then((element)=>{
     element.forEach(elements => {
     this.allUserDetails.push(elements.data())
     });

    })
  }
  
}
